//Images Import
import Back2 from "../../../Assets/Images/back1.jpg"
import Back1 from "../../../Assets/Images/back2.jpg"


//Lib Imports
import CommonLayout from "../../Layout/CommonLayout";
import ServiceCards from './ServiceCards/ServiceCards';
import SideBar from './ShopSideBar/SideBar'

const Shop = props => {
    return (
        <CommonLayout>
            <div class="container-fluid mt-5" style={{ backgroundColor: "LightGray" }}>
                <div class="row pt-2">
                    <SideBar/>
                    <div class="col-lg-9">
                        <div id="carouselExampleIndicators" class="carousel slide my-4" data-ride="carousel">
                            <ol class="carousel-indicators">
                                <li data-target="#carouselExampleIndicators" data-slide-to="0" class=""></li>
                                <li data-target="#carouselExampleIndicators" data-slide-to="1" class=""></li>
                                <li data-target="#carouselExampleIndicators" data-slide-to="2" class="active"></li>
                            </ol>
                            <div class="carousel-inner" role="listbox">
                                <div class="carousel-item">
                                    <img class="d-block img-fluid" src={Back1} alt="First slide" />
                                </div>
                                <div class="carousel-item">
                                    <img class="d-block img-fluid" src={Back2} alt="Second slide" />
                                </div>
                                <div class="carousel-item active">
                                    <img class="d-block img-fluid" src="http://placehold.it/900x350" alt="Third slide" />
                                </div>
                            </div>
                            <a class="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
                                <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                                <span class="sr-only">Previous</span>
                            </a>
                            <a class="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
                                <span class="carousel-control-next-icon" aria-hidden="true"></span>
                                <span class="sr-only">Next</span>
                            </a>
                        </div>
                        <div class="row">
                           <ServiceCards />
                        </div>
                    </div>
                </div>
            </div>
        </CommonLayout>
    );
}

export default Shop;