import React from 'react'
import { Link } from 'react-router-dom';

const onLogIn = () => {
    return(
        <>
            <li class="nav-item"><Link className="btn bg-light p-1" style={{fontSize: "14px"}} to="/login">Login</Link></li>
            <li class="nav-item"><Link className="nav-link btn btn-light bg-dark ml-1 p-1" style={{fontSize: "14px"}} to="/register">Register</Link></li>                
        </>
    );
}

export default onLogIn;