import { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import OnLogedIn from "../Logic/UserLoginLogic/OnLogedIn";
import OnLogIn from "../Logic/UserLoginLogic/OnLogin";

class NavBar extends Component{
    state = {
        notificationState: "none"
    }

    notifyBar = () => this.state.notificationState === "" ? 
                        this.setState({notificationState: "none"}) : 
                        this.setState({notificationState: ""})

    render(){
        console.log(this.props.user.displayName)
        return(
            <nav class=" navbar-expand-lg navbar navbar-dark bg-dark fixed-top py-3" id="mainNav">
                <div class="container">
                    <a class="navbar-brand js-scroll-trigger" href="#page-top">Fiverr React Demo</a>
                    <button class="navbar-toggler navbar-toggler-right" type="button" data-toggle="collapse" data-target="#navbarResponsive" aria-controls="navbarResponsive" aria-expanded="false" aria-label="Toggle navigation"><span class="navbar-toggler-icon"></span></button>
                    <div class="collapse navbar-collapse" id="navbarResponsive">
                        <ul class="navbar-nav ml-auto my-2 my-lg-0">
                            <li class="nav-item"><Link className="nav-link js-scroll-trigger" to="/">Home</Link></li>
                            <li class="nav-item"><Link className="nav-link js-scroll-trigger" to="/">Portfollio</Link></li>
                            <li class="nav-item"><Link className="nav-link js-scroll-trigger" to="/shop">Shop</Link></li>
                            <li class="nav-item"><Link className="nav-link js-scroll-trigger" to="/">Contact Us</Link></li>
                            {
                            this.props.user.loggedinStatus?
                                <OnLogedIn 
                                displayName={this.props.user.displayName} 
                                pictureURL = {this.props.user.pictureURL} />:
                                <OnLogIn />
                            }
                        </ul>
                    </div>
                </div>
            </nav>
        );   
    } 
}
const mapStatetoProps = state => {
    return{
        user: state.loggedinUser
    }
}


export default connect(mapStatetoProps,null)(NavBar);